<template>
    <div>
        <b-row>
            <b-col md="3" sm="12">
                <div class="box box-danger w-100">
                    <div class="box-header with-border d-flex justify-content-between align-items-center">
                        <span>
                            <h3 id="titulo_laudo" class="box-title">Modo Edição - {{ tipo }}</h3>
                            <br>
                            <h3 class="box-subtitle">
                                Edite as categorias abaixo
                            </h3>
                        </span>
                        <span>
                            <b-button size="md" variant="primary" @click="salvar()" :disabled="!modificou">
                                <font-awesome-icon class="mr-1" :icon="['fas', 'save']" />
                                Salvar
                            </b-button>
                        </span>
                    </div>
                    <div class="box-body" style="max-height: calc(100vh - 235px);overflow-y: auto;">
                        <el-tree accordion :data="items_laudo" :props="defaultProps" node-key="id" ref="tree"
                            style="font-weight: 500; user-select: none;" draggable :expand-on-click-node="true"
                            @node-contextmenu="menuContexto" @node-click="clickItemLaudo">
                            <span class="custom-tree-node" slot-scope="{ node, data }">
                                <font-awesome-icon :icon="['fas', `${ data.menu ? 'circle-notch' : 'file-alt'}`]"
                                    style="width: 14px;font-size: 0.9em; padding-right: 4px;"
                                    :class="'text-'+data.cor" />
                                <span :class="'text-'+data.cor">{{ node.label }}</span>
                            </span>
                        </el-tree>

                        <context-menu id="context-menu" ref="ctxMenu">
                            <!--<span>Menu item: {{itemAtual.nome}}</span>-->
                            <li v-for="item_menu of mnuContexto" @click="item_menu.funcao" :key="'mn-'+item_menu.id"
                                :style="`color: ${item_menu.cor}`">
                                <font-awesome-icon :icon="['fas', item_menu.icone]"
                                    style="width: 14px;font-size: 0.9em; padding-right: 4px;" />
                                {{ item_menu.nome }}</li>
                        </context-menu>

                        <!--<laudos-tree v-if="items_laudo && !edicao" :items="items_laudo"></laudos-tree>-->
                    </div>
                </div>
            </b-col>

            <b-col md="9" sm="12" v-if="itemAtual && !itemAtual.menu">
                <div class="box box-danger w-100">
                    <div class="box-header with-border d-flex justify-content-between align-items-center">
                        <span>
                            <h3 id="titulo_laudo" class="box-title">Editar item</h3>
                            <br>
                            <h3 class="box-subtitle">
                                ...
                            </h3>
                        </span>
                    </div>

                    <div class="box-body" style="max-height: calc(100vh - 235px);overflow-y: auto;">
                        <b-row align-v="center" class="d-flex justify-content-between align-items-center mb-2">
                            <b-col align-v="center" align="left" md="12" sm="12">
                                <b-input-group prepend="Nome">
                                    <b-form-input v-model="itemAtual.nome"></b-form-input>
                                </b-input-group>
                            </b-col>
                        </b-row>

                        <b-row align-v="center" class="d-flex justify-content-between align-items-center mb-2">
                            <b-col align-v="center" align="left" md="12" sm="12">
                                <b-input-group prepend="Tipos">
                                    <el-select v-model="valores" multiple placeholder="Selecione um tipo"
                                        style="width: 420px" @remove-tag="removeProp">
                                        <el-option v-for="item in opcoes" :key="item.value" :label="item.label"
                                            :value="item.value">
                                        </el-option>
                                    </el-select>
                                </b-input-group>
                            </b-col>
                        </b-row>

                        <b-card no-body>
                            <b-tabs pills card vertical>
                                <b-tab lazy v-if="valores.includes('texto')" title="Texto"
                                    :active="valores[0] == 'texto'" class="p-0">
                                    <mc-wysiwyg id="editarea1" v-model="itemAtual.texto" :hide="hide" :height="150"
                                        style="text-align: left"></mc-wysiwyg>
                                </b-tab>
                                <b-tab lazy v-if="valores.includes('conclusao')" title="Conclusão"
                                    :active="valores[0] == 'conclusao'" class="p-0">
                                    <mc-wysiwyg id="editarea2" v-model="itemAtual.conclusao" :hide="hide" :height="150"
                                        style="text-align: left"></mc-wysiwyg>
                                </b-tab>
                                <b-tab lazy v-if="valores.includes('obs')" title="Observação"
                                    :active="valores[0] == 'obs'" class="p-0">
                                    <mc-wysiwyg id="editarea3" v-model="itemAtual.obs" :hide="hide" :height="150"
                                        style="text-align: left"></mc-wysiwyg>
                                </b-tab>
                                <b-tab lazy v-if="valores.includes('ref')" title="Referência"
                                    :active="valores[0] == 'ref'" class="p-0">
                                    <mc-wysiwyg id="editarea4" v-model="itemAtual.ref" :hide="hide" :height="150"
                                        style="text-align: left"></mc-wysiwyg>
                                </b-tab>
                            </b-tabs>
                        </b-card>

                        <!--<div id="texto" v-html="itemAtual"
                            style="font-size: 12px; user-select: none; text-align: left" />-->
                    </div>
                </div>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    const clipboard = require("@/_helpers/clipboard");
    import contextMenu from 'vue-context-menu';
    import {
        McWysiwyg
    } from '@mycure/vue-wysiwyg';

    export default {
        name: "ChecklistDemanda",
        components: {
            contextMenu,
            McWysiwyg
        },
        computed: {
            user() {
                return this.$store.state.auth.user;
            },
            mensagem() {
                let texto = '';
                let conclusao = '';
                let obs = '';
                let ref = '';

                this.items_selecionados.forEach(el => texto += el.texto ? `${el.texto}<p>` : ``);

                this.items_selecionados.forEach(el => conclusao += el.conclusao ? `${el.conclusao}<p>` : ``);
                texto += conclusao.length > 0 ? `<p><b>Conclusão:</b><p>${conclusao}` : ``;

                this.items_selecionados.forEach(el => obs += el.obs ? `${el.obs}<p>` : ``);
                texto += obs.length > 0 ? `<p><b>Obs:</b><p>${obs}` : ``;

                this.items_selecionados.forEach(el => ref += el.ref ? `${el.ref}<p>` : ``);
                texto += ref.length > 0 ? `<p><b>Ref:</b><p>${ref}` : ``;
                return texto;
            }
        },
        props: ["id"],
        data() {
            return {
                items_laudo: [],
                items_selecionados: [],
                defaultProps: {
                    children: 'menu',
                    label: 'nome',
                    isLeaf: 'menu'
                },
                txtBotaoCopiar: "Copiar Texto",
                clsBotaoCopiar: "primary",
                text: '',
                tipo: '',
                mnuContexto: [],
                itemAtual: null,
                modificou: true,
                hide: {
                    strikethrough: true,
                    heading: true,
                    alignLeft: true,
                    alignCenter: true,
                    alignRight: true,
                    ol: true,
                    ul: true,
                    url: true,
                    table: true,
                    indent: true,
                    outdent: true
                },
                opcoes: [{
                    value: 'texto',
                    label: 'Texto'
                }, {
                    value: 'conclusao',
                    label: 'Conclusão'
                }, {
                    value: 'obs',
                    label: 'Observação'
                }, {
                    value: 'ref',
                    label: 'Referência'
                }],
                valores: []
            };
        },
        async mounted() {
            await this.getItemsLaudo();
        },
        methods: {
            makeid(length) {
                var result = '';
                var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
                var charactersLength = characters.length;
                for (var i = 0; i < length; i++) {
                    result += characters.charAt(Math.floor(Math.random() * charactersLength));
                }
                return result;
            },
            removeAt(idx) {
                this.items_selecionados.splice(idx, 1);
            },
            clickItemLaudo(data) {
                console.log(JSON.stringify(data));
                for (var prop in this.itemAtual) {
                    if (this.itemAtual[prop] == '' || this.itemAtual[prop] == '<br>') delete this.itemAtual[prop];
                }
                if (!data.menu) {
                    this.itemAtual = data;
                    this.valores = [];
                    if (this.itemAtual.texto) this.valores.push('texto');
                    if (this.itemAtual.conclusao) this.valores.push('conclusao');
                    if (this.itemAtual.obs) this.valores.push('obs');
                    if (this.itemAtual.ref) this.valores.push('ref');
                    this.$nextTick(() => {
                        document.getElementById('editor').innerHTML = '';
                    });
                }
            },
            removeProp(prop) {
                delete this.itemAtual[prop];
            },
            menuContexto(event, data) {
                //this.mnuContexto
                this.mnuContexto = [];
                console.log(event);
                console.clear();
                this.itemAtual = data;
                const itemExcluir = {
                    id: 1,
                    //nome: `Excluir item ${data.nome}`,
                    nome: `Excluir ${data.menu ? 'categoria' : 'item'}`,
                    icone: 'trash',
                    cor: 'darkred',
                    funcao: () => {
                        //const n = this.$refs.tree.getNode(this.itemAtual.id);
                        this.$confirm(`Deseja realmente excluir ${data.menu ? 'a categoria' : 'o item'}?`,
                                'Exclusão', {
                                    confirmButtonText: 'OK',
                                    cancelButtonText: 'Cancel',
                                    type: 'warning'
                                }).then(value => {
                                if (value) this.$refs.tree.remove(this.itemAtual.id);
                                this.itemAtual = null;
                            })
                            .catch(err => {
                                this.toast(`Não foi possível efetuar a operação: ${err}`, false);
                            })
                    }
                };
                const categoriaAdicionar = {
                    id: 2,
                    nome: `Adicionar categoria`,
                    icone: 'plus',
                    cor: 'black',
                    funcao: async () => {
                        const nome = await this.$prompt('Digite o nome da categoria', 'Nova categoria', {
                            confirmButtonText: 'OK',
                            cancelButtonText: 'Cancel'
                        });
                        const newItem = {
                            id: this.makeid(10),
                            nome: nome.value,
                            cor: this.itemAtual.cor,
                            ordem: this.itemAtual.ordem,
                            menu: []
                        };
                        this.$refs.tree.append(newItem, this.itemAtual.id);
                    }
                };
                const itemAdicionar = {
                    id: 3,
                    nome: `Adicionar item`,
                    icone: 'plus',
                    cor: 'black',
                    funcao: () => {
                        const newItem = {
                            id: this.makeid(10),
                            nome: `Novo item`,
                            cor: this.itemAtual.cor,
                            ordem: this.itemAtual.ordem
                        };
                        console.log(newItem);
                        const n = this.$refs.tree.getNode(this.itemAtual.id);
                        console.log(n);
                        console.log(this.itemAtual);
                        this.$refs.tree.append(newItem, this.itemAtual.id);
                        //n.childNodes.push(newItem);
                    }
                };
                const categoriaEditar = {
                    id: 4,
                    nome: `Alterar nome`,
                    icone: 'edit',
                    cor: 'black',
                    funcao: async () => {
                        const nome = await this.$prompt('Digite o novo nome da categoria', 'Editar categoria', {
                            confirmButtonText: 'OK',
                            cancelButtonText: 'Cancel',
                            inputValue: this.itemAtual.nome
                        });
                        this.itemAtual.nome = nome.value;
                    }
                };

                if (data.menu) {
                    this.mnuContexto.push(categoriaEditar);
                    this.mnuContexto.push(categoriaAdicionar);
                    this.mnuContexto.push(itemAdicionar);
                }
                this.mnuContexto.push(itemExcluir);
                this.$refs.ctxMenu.open();
            },
            async getItemsLaudo() {
                try {
                    this.getTipo();
                    console.log(this.tipo);
                    const items = (await this.$http.get(`/api/texto/${this.id}`)).data;
                    items.forEach(el => {
                        if (el.menu) {
                            el.visible = false;
                            this.setIdTree(el.menu, el.ordem);
                            this.setCorItems(el.menu, el.ordem);
                        }
                        el.id = this.makeid(10);
                        el.cor = el.ordem;
                        delete el._id;
                    });
                    this.items_laudo = items.sort((a, b) =>
                        parseInt(a.ordem) > parseInt(b.ordem) ?
                        1 :
                        parseInt(b.ordem) > parseInt(a.ordem) ?
                        -1 :
                        0
                    );
                } catch (error) {
                    this.toast(error.data ? error.data.message || error.data : error.statusText, false);
                }
            },
            setIdTree(menu) {
                menu.forEach(el => {
                    if (el.menu) {
                        this.setIdTree(el.menu);
                    }
                    el.id = this.makeid(10);
                });
            },
            setCorItems(menu, cor) {
                menu.forEach(el => {
                    if (el.menu) {
                        this.setCorItems(el.menu, cor);
                    }
                    el.cor = cor;
                });
            },
            async salvar() {
                const items = this.items_laudo;

                items.forEach((el, idx) => {
                    el.ordem = idx + 1;
                    for (var prop in el) {
                        if (el[prop] == '' || el[prop] == '<br>') delete el[prop];
                    }
                    delete el.id;
                });
                const novos_items = (await this.$http.post(`/api/texto/${this.id}`, items)).data;
                console.log("salvei: ");
                console.log(novos_items);
            },
            getTipo() {
                switch (parseInt(this.id)) {
                    case 1:
                        this.tipo = 'Colonoscopia';
                        break;
                    case 2:
                        this.tipo = 'EDA';
                        break;
                    case 3:
                        this.tipo = 'pHmetria';
                        break;
                    case 4:
                        this.tipo = 'Teste Respiratório';
                        break;
                }
            },
            toast(mensagem, sucesso) {
                const title = sucesso ? "Sucesso" : "Erro";
                const variant = sucesso ? "success" : "danger";
                this.$root.$bvToast.toast(mensagem, {
                    title: title,
                    variant: variant,
                    solid: true,
                });
            },
            copiarTexto() {
                clipboard.copiar(this.mensagem);
                this.txtBotaoCopiar = "Copiado!"
                this.clsBotaoCopiar = "success";
                setTimeout(() => {
                    this.txtBotaoCopiar = "Copiar Texto";
                    this.clsBotaoCopiar = "primary";
                }, 1500);
            },
            highlightSearch(obj, texto, cor) {
                texto = texto.replace(/([.?*+^$[\]\\(){}|-])/g, "\\$1");
                var query = new RegExp("((\\B|\\b)" + texto + "(\\B|\\b))", "gm");
                var newe = obj.replace(query,
                    `<mark class="bg-${cor}" style="padding: 2px" ${this.$options._scopeId}>$1</mark>`);
                this.text = newe;
            },
            run(item) {
                this.text = this.mensagem;
                if (item.texto) {
                    let fragmentos_texto = item.texto.split("<p>");
                    for (let i = 0; i < fragmentos_texto.length; i++) {
                        this.highlightSearch(this.text, fragmentos_texto[i], item.cor);
                    }
                }
                if (item.conclusao) {
                    let fragmentos_conclusao = item.conclusao.split("<p>");
                    for (let i = 0; i < fragmentos_conclusao.length; i++) {
                        this.highlightSearch(this.text, fragmentos_conclusao[i], item.cor);
                    }
                }
                if (item.obs) {
                    let fragmentos_obs = item.obs.split("<p>");
                    for (let i = 0; i < fragmentos_obs.length; i++) {
                        this.highlightSearch(this.text, fragmentos_obs[i], item.cor);
                    }
                }

                if (item.ref) {
                    let fragmentos_ref = item.ref.split("<p>");
                    for (let i = 0; i < fragmentos_ref.length; i++) {
                        this.highlightSearch(this.text, fragmentos_ref[i], item.cor);
                    }
                }
            },
            back() {
                this.items_selecionados.push({});
                this.items_selecionados.pop();
                this.text = this.mensagem;
            }
        }
    };
</script>
