<template>
  <b-navbar v-if="user" class="menu-app">
    <b-navbar-nav>
      <b-nav-text class="menu-header">MENU PRINCIPAL</b-nav-text>
      <template v-for="menu of menus">
        <b-nav-item v-if="!menu.submenus && showMenu(menu.roles)" :to="menu.target" :key="menu.id">
          <i v-if="menu.icon" :class="menu.icon"></i>
          {{ menu.title }}
        </b-nav-item>

        <b-nav-item
          v-if="menu.submenus && showMenu(menu.roles)"
          :aria-expanded="menu.visible ? 'true' : 'false'"
          :aria-controls="menu.id"
          @click="Open(menu)"
          :to="menu.target"
          :text="menu.title"
          :key="menu.id"
        >
          <font-awesome-icon v-if="menu.icon" :icon="['fas', `${menu.icon}`]" class="mr-2" size="1x" />
          {{ menu.title }}
          <font-awesome-icon
            :icon="['fas', 'angle-left']"
            size="1x"
            class="pull-right"
            :class="menu.visible ? 'open' : 'closed'"
          />
        </b-nav-item>
        <b-collapse
          v-if="menu.submenus"
          :id="menu.id"
          :key="menu.id+'c'"
          v-model="menu.visible"
          tag="ul"
        >
          <template v-for="submenu of menu.submenus">
            <b-nav-item v-if="showMenu(submenu.roles)" :key="submenu.id" :to="submenu.target">
              <font-awesome-icon v-if="submenu.icon" :icon="['fas', `${submenu.icon}`]" class="mr-2" />
              {{ submenu.title }}
            </b-nav-item>
          </template>
        </b-collapse>
      </template>
    </b-navbar-nav>
  </b-navbar>
</template>

<script>
export default {
  name: "CompMenu",
  data() {
    return {
      hover: false,
      visible: false,
      menus: []
    };
  },
  computed: {
    user() {
      return this.$store.state.auth.user;
    }
  },
  mounted() {
    let menus = this.$project.menus;
    for (let menu in menus) {
      menus[menu].visible = false;
    }
    this.menus = menus;
  },
  methods: {
    showMenu(roles) {
      let user = this.$store.state.auth.user;
      if (roles && roles.indexOf(user.role) < 0) {
        return false;
      }
      return true;
    },
    Open(menu){
      const visivel = !menu.visible;
      this.menus.forEach(el => el.visible = false);
      menu.visible = visivel;
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/styles/variables";

.navbar.menu-app {
  display: block;
  background-color: #007eb8;
  position: fixed;
  top: 0;
  left: 0;
  padding-left: 0;
  padding-right: 0;
  padding-top: 50px;
  min-height: 100%;
  width: 230px;
  z-index: 810;
  user-select: none;
  //font-family: $font-family-menu;

  .navbar-text {
    color: $cor-paleta-zumthor;
    background: #007bb7;
    font-size: $font-size-menu-header;
    text-align: left;
    padding: 10px;
    user-select: none;
    cursor: default;
  }

  .navbar-nav {
    flex-direction: column;

    ul {
      list-style: none;
      text-align: left;
      padding-left: 5px;
      background-color: #0089b6;
    }
  }

  .nav-item {
    position: relative;
    margin: 0;
    padding: 0;
    text-align: left;

    .nav-link {
      color: $cor-itens-menu;
      font-size: 13px;
      padding: 12px 5px 12px 15px;
      display: block;
      float: left;
      width: 100%;
      font-weight: 500;

      .pull-right {
        position: absolute;
        right: 15px;
        top: 50%;
        margin-top: -7px;
      }

      .open {
        -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
        -o-transform: rotate(-90deg);
        transform: rotate(-90deg);
        transition: transform 0.5s ease;
      }

      .closed {
        transition: transform 0.5s ease;
      }
    }

    .dropdown-menu {
      background-color: $cor-paleta-light-grey;
      padding: 0;

      .dropdown-item {
        float: none;
        color: $cor-itens-menu;
        padding: 12px 5px 12px 15px;
        text-decoration: none;
        display: block;
        text-align: left;
        width: 100%;
        text-decoration: normal;
        font-size: $font-size-base;
      }

      .dropdown-item:hover {
        background-color: $cor-paleta-light-teal;
      }
    }
  }

  .nav-item:hover {
    background-color: #008ecc;
  }

  .nav-item:hover a {
    color: white !important;
  }
}
</style>