<template>
  <div id="app">
    <Loader :isVisible="isLoading"></Loader>
    <CompHeader></CompHeader>
    <CompMenu></CompMenu>
    <div :class="[this.$store.state.auth.user ? 'content ml-230' : 'content']">
      <router-view :key="$route.fullPath"></router-view>
    </div>
    <CompFooter></CompFooter>
  </div>
</template>

<script>
import CompHeader from "@/components/layout/CompHeader";
import CompFooter from "@/components/layout/CompFooter";
import CompMenu from "@/components/layout/CompMenu";
import Loader from "@/components/layout/Loader";

export default {
  name: "App",
  created() {
    document.title = this.$project.id.replace('.','') + " - " + this.$project.name;
  },
  components: {
    CompHeader,
    CompFooter,
    CompMenu,
    Loader
  },
  mounted() {
    this.enableInterceptor();
  },
  data() {
    return {
      isLoading: false,
      axiosInterceptor: null
    };
  },
  methods: {
    enableInterceptor() {
      this.axiosInterceptor = this.$http.interceptors.request.use(
        config => {
          this.isLoading = true;
          return config;
        },
        error => {
          this.isLoading = false;
          return Promise.reject(error);
        }
      );

      this.$http.interceptors.response.use(
        response => {
          this.isLoading = false;
          return response;
        },
        error => {
          this.isLoading = false;
          return Promise.reject(error);
        }
      );
    },

    disableInterceptor() {
      this.$http.interceptors.request.eject(this.axiosInterceptor);
    }
  }
};
</script>

<style>
#app {
  font-family: "Helvetica Neue", Helvetica;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height: 100%;
  color: #2c3e50;
  /*margin-top: 60px;*/
  margin-bottom: 50px;
  /*position: fixed;*/
}
.content {
  padding: 20px;
  margin-top: 50px;
  background-color: #ecf0f5;
  min-height: calc(100vh - 100px);
  /* position: fixed;
  height: 100%;
  width: 100%; */
}

.ml-230 {
  margin-left: 230px;
}
</style>
