<template>
  <div>
  <b-navbar class="header" toggleable="lg" type="dark" variant="info" fixed="top">
    <b-navbar-brand href="#">
      <font-awesome-icon :icon="['fas', 'stethoscope']" size="lg" />
      <span class="app-prefixo">{{ brand[0] }}</span>
      <span class="app-sufixo" v-if="brand[1]">{{ brand[1] }}</span>
    </b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto" v-if="user" >
        <b-nav-item-dropdown right>
          <!-- Using 'button-content' slot -->
          <template v-slot:button-content>
            <font-awesome-icon :icon="['fas', 'user-circle']" size="2x" />
          </template>
          <b-dropdown-item disabled>{{ user.nomeCompleto }}</b-dropdown-item>
            <b-dropdown-item disabled>Perfil: {{ user.role }}</b-dropdown-item>
            <b-dropdown-divider></b-dropdown-divider>
            <b-dropdown-item-button @click="logout">Sair</b-dropdown-item-button>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</div>
</template>

<script>
import router from '@/router'

export default {
  name: "CompHeader",
  computed: {
    brand() {
      return this.$project.id.split(".");
    },
    user() {
      return this.$store.state.auth.user;
    }
  },
  methods: {
    logout() {
      this.$store.dispatch("auth/logout");
      router.push({name: 'Login'});
    }
  }
};
</script>

