import '@babel/polyfill'
import 'mutationobserver-shim'
import Vue from 'vue'
import '@/_plugins/bootstrap-vue'
import '@/_plugins/fontawesome'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue'
import Api from '@/_plugins/axios'
import Sjcl from '@/_plugins/sjcl'
import '@/assets/styles/main.scss'
import router from '@/router'
import store from '@/store'
import CompTable from '@/components/CompTable'
import CompModalForm from '@/components/CompModalForm'
import CompMoney from '@/components/CompMoney'
import CompTree from '@/components/CompTree'
import CompTreeNode from '@/components/CompTreeNode'
import Vuelidate from 'vuelidate'
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue'
import VueMask from 'v-mask'
import money from 'v-money'


Vue.config.productionTip = false;
Vue.prototype.$project = require('@/project.json');
Vue.prototype.$http = Api;
Vue.prototype.$encr = Sjcl;
Vue.component('cx-table', CompTable);
Vue.component('cx-modal-form', CompModalForm);
Vue.component('cx-money', CompMoney);
Vue.component('laudos-tree', CompTree);
Vue.component('laudos-tree-node', CompTreeNode);
Vue.use(Vuelidate);
Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);
Vue.use(VueMask);
Vue.use(money, {
  decimal: ',',
  thousands: '.',
  prefix: 'R$ ',
  precision: 2,
  masked: false
});
Vue.use(ElementUI);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')