<template>
	<div>
		<b-row>
			<b-col md="3" sm="12">
				<div class="box box-primary w-100">
					<div class="box-header with-border d-flex justify-content-between align-items-center">
						<span>
							<h3 id="titulo_laudo" class="box-title">Novo Laudo - {{ tipo }}</h3>
							<br>
							<h3 class="box-subtitle">
								Crie um novo laudo selecionando um texto entre as categorias abaixo
							</h3>
						</span>
					</div>
					<div class="box-body" style="max-height: calc(100vh - 235px);overflow-y: auto;">
						<el-tree accordion :data="items_laudo" :props="defaultProps" node-key="id" ref="tree"
							style="font-weight: 500; user-select: none;" :expand-on-click-node="true"
							@node-click="clickItemLaudo">
							<span class="custom-tree-node" slot-scope="{ node, data }">
								<font-awesome-icon v-if="!data.menu" :icon="['fas', 'file-alt']"
									style="width: 14px;font-size: 0.9em; padding-right: 4px;"
									:class="'text-'+data.cor" />
								<span :class="'text-'+data.cor">{{ node.label }}</span>
							</span>
						</el-tree>
					</div>
				</div>
			</b-col>

			<b-col md="3" sm="12" v-if="items_selecionados.length">
				<div class="box box-primary w-100">
					<div class="box-header with-border">
						<h3 id="titulo_laudo" class="box-title">Itens selecionados</h3>
						<br>
						<h3 class="box-subtitle">
							Arraste para reordenar os itens
						</h3>
					</div>
					<draggable tag="ul" :list="items_selecionados" class="list-group" handle=".handle">
						<li :class="`list-group-item bg-${element.cor} d-flex justify-content-between align-items-center`"
							v-for="(element, idx) in items_selecionados" :key="element.nome"
							style="text-align: left; padding: 9px 4px 9px 14px; overflow: visible; height: 40px; border: 0px; border-radius: 0px; user-select: none;"
							@mouseover="destacar(element)" @mouseout="back()">

							<span>
								<font-awesome-icon class="icon-action handle" :icon="['fas', 'list']"
									style="cursor: move" />
								<span class="text ml-3">{{ element.nome }} </span>
							</span>
							<font-awesome-icon class="icon-action mr-2" :icon="['fas', 'trash']" @click="removeAt(idx)"
								style="cursor: pointer" />

						</li>
					</draggable>
				</div>
			</b-col>
			<b-col md="6" sm="12" v-if="items_selecionados.length">
				<div class="box box-primary w-100">
					<div class="box-header with-border d-flex justify-content-between align-items-center">
						<span>
							<h3 id="titulo_laudo" class="box-title">Texto</h3>
							<br>
							<h3 class="box-subtitle">
								Clique no botão à direita para copiar o texto
							</h3>
						</span>
						<span>
							<b-button size="md" :variant="clsBotaoCopiar" @click="copiarTexto()">
								{{ txtBotaoCopiar }}
							</b-button>
						</span>
					</div>
					<div class="box-body" style="max-height: calc(100vh - 235px);overflow-y: auto;">
						<div id="texto" v-html="text" style="font-size: 12px; user-select: none; text-align: left" />
					</div>
				</div>
			</b-col>
		</b-row>
	</div>
</template>

<script>
	const clipboard = require("@/_helpers/clipboard");
	import draggable from 'vuedraggable';

	export default {
		name: "ChecklistDemanda",
		components: {
			draggable
		},
		computed: {
			user() {
				return this.$store.state.auth.user;
			},
			mensagem() {
				let texto = '';
				let conclusao = '';
				let obs = '';
				let ref = '';
				let ind = 1;

				this.items_selecionados.forEach(el => texto += el.texto ? `${el.texto}<p>` : ``);

				this.items_selecionados.forEach(el => {
					conclusao += el.conclusao ? `${ind++} - ${el.conclusao}<p>` : ``;
				});
				texto += conclusao.length > 0 ? `<p><b>Conclusão:</b><p>${conclusao}` : ``;

				this.items_selecionados.forEach(el => obs += el.obs ? `${el.obs}<p>` : ``);
				texto += obs.length > 0 ? `<p><b>Obs:</b><p>${obs}` : ``;

				this.items_selecionados.forEach(el => ref += el.ref ? `${el.ref}<p>` : ``);
				texto += ref.length > 0 ? `<p><b>Ref:</b><p>${ref}` : ``;
				return texto;
			}
		},
		props: ["id"],
		data() {
			return {
				items_laudo: [],
				items_selecionados: [],
				defaultProps: {
					children: 'menu',
					label: 'nome',
					isLeaf: 'menu'
				},
				txtBotaoCopiar: "Copiar Texto",
				clsBotaoCopiar: "primary",
				txtBotaoEditar: ["Editar", "Salvar"],
				clsBotaoEditar: ["primary", "success"],
				icoBotaoEditar: ["edit", "save"],
				edicao: false,
				text: '',
				tipo: '',
				mnuContexto: [],
				itemAtual: null
			};
		},
		async mounted() {
			await this.getItemsLaudo();
		},
		methods: {
			makeid(length) {
				var result = '';
				var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
				var charactersLength = characters.length;
				for (var i = 0; i < length; i++) {
					result += characters.charAt(Math.floor(Math.random() * charactersLength));
				}
				return result;
			},
			removeAt(idx) {
				this.items_selecionados.splice(idx, 1);
			},
			clickItemLaudo(data) {
				if (!data.menu) {
					const item_selecionado = data;
					console.log(data.id );
					const existe = this.items_selecionados.filter(el => el.id == data.id).length > 0;
					if (!existe) this.items_selecionados.push(item_selecionado);
					this.text = this.mensagem;	
				}
			},
			async getItemsLaudo() {
				try {
					this.getTipo();
					console.log(this.tipo);
					const items = (await this.$http.get(`/api/texto/${this.id}`)).data;
					items.forEach(el => {
						if (el.menu) {
							el.visible = false;
							this.setIdTree(el.menu, el.ordem);
							this.setCorItems(el.menu, el.ordem);
						}
						el.id = this.makeid(10);
						el.cor = el.ordem;
					});
					this.items_laudo = items.sort((a, b) =>
						parseInt(a.ordem) > parseInt(b.ordem) ?
						1 :
						parseInt(b.ordem) > parseInt(a.ordem) ?
						-1 :
						0
					);
				} catch (error) {
					this.toast(error.data ? error.data.message || error.data : error.statusText, false);
				}
			},
			setIdTree(menu) {
				menu.forEach(el => {
					if (el.menu) {
						this.setIdTree(el.menu);
					}
					el.id = this.makeid(10);
				});
			},
			setCorItems(menu, cor) {
				menu.forEach(el => {
					if (el.menu) {
						this.setCorItems(el.menu, cor);
					}
					el.cor = cor;
				});
			},
			getTipo() {
				switch (parseInt(this.id)) {
					case 1:
						this.tipo = 'Colonoscopia';
						break;
					case 2:
						this.tipo = 'EDA';
						break;
					case 3:
						this.tipo = 'pHmetria';
						break;
					case 4:
						this.tipo = 'Teste Respiratório';
						break;
				}
			},
			toast(mensagem, sucesso) {
				const title = sucesso ? "Sucesso" : "Erro";
				const variant = sucesso ? "success" : "danger";
				this.$root.$bvToast.toast(mensagem, {
					title: title,
					variant: variant,
					solid: true,
				});
			},
			copiarTexto() {
				clipboard.copiar(this.mensagem);
				this.txtBotaoCopiar = "Copiado!"
				this.clsBotaoCopiar = "success";
				setTimeout(() => {
					this.txtBotaoCopiar = "Copiar Texto";
					this.clsBotaoCopiar = "primary";
				}, 1500);
			},
			highlightSearch(obj, texto, cor) {
				texto = texto.replace(/([.?*+^$[\]\\(){}|-])/g, "\\$1");
				var query = new RegExp("((\\B|\\b)" + texto + "(\\B|\\b))", "gm");
				var newe = obj.replace(query,
					`<mark class="bg-${cor}" style="padding: 2px" ${this.$options._scopeId}>$1</mark>`);
				this.text = newe;
			},
			destacar(item) {
				this.text = this.mensagem;
				if (item.texto) {
					let fragmentos_texto = item.texto.split("<p>");
					for (let i = 0; i < fragmentos_texto.length; i++) {
						this.highlightSearch(this.text, fragmentos_texto[i], item.cor);
					}
				}
				if (item.conclusao) {
					let fragmentos_conclusao = item.conclusao.split("<p>");
					for (let i = 0; i < fragmentos_conclusao.length; i++) {
						this.highlightSearch(this.text, fragmentos_conclusao[i], item.cor);
					}
				}
				if (item.obs) {
					let fragmentos_obs = item.obs.split("<p>");
					for (let i = 0; i < fragmentos_obs.length; i++) {
						this.highlightSearch(this.text, fragmentos_obs[i], item.cor);
					}
				}

				if (item.ref) {
					let fragmentos_ref = item.ref.split("<p>");
					for (let i = 0; i < fragmentos_ref.length; i++) {
						this.highlightSearch(this.text, fragmentos_ref[i], item.cor);
					}
				}
			},
			back() {
				this.items_selecionados.push({});
				this.items_selecionados.pop();
				this.text = this.mensagem;
			}
		}
	};
</script>
