<template>
  <div>
    <div class="box-header">
      <br />
      <div class="box-tools-left">
        <div class="input-group input-group-sm float-left">
          <slot></slot>
        </div>
      </div>
      <div class="box-tools-right">
        <div class="input-group input-group-sm hidden-xs" style="width: 200px;" v-if="filter">
          <b-form-input
            v-model.trim="filterInput"
            type="search"
            placeholder="Filtre os dados aqui"
          />
          <div class="input-group-btn">
            <button type="submit" class="btn btn-default">
              <font-awesome-icon :icon="['fas', 'search']" />
            </button>
          </div>
        </div>
      </div>
    </div>
    <b-table
      class="table-bordered shadow-sm"
      :items="items"
      :fields="fieldsComp"
      striped
      hover
      show-empty
      empty-text="Não foram encontrados dados."
      stacked="sm"
      :small="small"
      :filter="filterInput"
      :sort-compare-options="{ numeric: true, sensitivity: 'base' }"
      :filter-function="nameFilter"
      :per-page="perPageIntern"
      :current-page="currentPage"
      :sort-by.sync="sortByIntern"
      :sort-desc.sync="sortDescIntern"
      no-sort-reset
      @filtered="onFiltered"
    >
      <template v-slot:cell(actions)="row">
        <font-awesome-icon
          v-for="action of actions"
          :key="action.id"
          class="table-action"
          :icon="action.icon"
          size="lg"
          @click="action.action(row.item)"
          v-b-tooltip.hover
          :title="action.tooltip"
        />
      </template>
    </b-table>
    <template v-if="hasPagination">
      <b-pagination
        v-model="currentPage"
        :total-rows="totalRows"
        :per-page="perPageIntern"
        align="center"
        class="pull-right"
      >
        <template slot="first-text">
          <font-awesome-icon :icon="['fas', 'angle-double-left']" />
        </template>
        <template slot="prev-text">
          <font-awesome-icon :icon="['fas', 'angle-left']" />
        </template>
        <template slot="next-text">
          <font-awesome-icon :icon="['fas', 'angle-right']" />
        </template>
        <template slot="last-text">
          <font-awesome-icon :icon="['fas', 'angle-double-right']" />
        </template>
      </b-pagination>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    fields: {
      type: Array,
      required: true
    },
    items: {
      type: [Array, Function],
      required: true
    },
    actions: {
      type: Array,
      default: null
    },
    filter: {
      type: Boolean,
      default: false
    },
    perPage: {
      type: Number,
      default: 10
    },
    sortBy: {
      type: String,
      default: null
    },
    sortDesc: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      filterInput: "",
      perPageIntern: this.perPage,
      sortByIntern: this.sortBy,
      sortDescIntern: this.sortDesc,
      totalRows: this.items.length,
      pageOptions: [
        {
          text: "10 linhas",
          value: 10
        },
        {
          text: "20 linhas",
          value: 20
        },
        {
          text: "50 linhas",
          value: 50
        },
        {
          text: "Todas as linhas",
          value: null
        }
      ],
      currentPage: 1
    };
  },
  computed: {
    fieldsComp() {
      var comp = this.fields;
      if (this.actions) {
        comp.push({
          key: "actions",
          label: "Ações",
          sortable: false
        });
      }
      return comp;
    },
    hasPagination() {
      return this.perPageIntern && this.totalRows > this.perPageIntern;
    }
  },
  watch: {
    items: function(items) {
      this.totalRows = items.length;
    }
  },
  methods: {
    // Ignora acentos e só busca nas colunas apresentadas
    nameFilter(row, filter) {
      var rowValues = "";
      for (var [key, value] of Object.entries(row)) {
        if (this.fields.find(obj => obj.key === key)) {
          rowValues = rowValues + "," + value;
        }
      }
      if (!filter || filter === "") {
        return true;
      }
      var n = this.prepareNameFilter(rowValues);
      var s = this.prepareNameFilter(filter);
      return n.includes(s);
    },
    // Limpeza de acentos
    prepareNameFilter(str) {
      if (!str) return "";
      str = String(str).toLowerCase();
      str = str.replace(/[aáàãäâ]/, "a");
      str = str.replace(/[eéèëê]/, "e");
      str = str.replace(/[iíìïî]/, "i");
      str = str.replace(/[oóòõöô]/, "o");
      str = str.replace(/[uúùüû]/, "u");
      return str;
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
</script>

<style lang='scss'>
@import "@/assets/styles/variables";

.box {
  position: relative;
  border-radius: 3px;
  background: #ffffff;
  border-top: 3px solid #d2d6de;
  margin-bottom: 20px;
  width: 100%;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}

.box-header {
  color: #444;
  display: block;
  padding: 10px;
  position: relative;
  text-align: left;
  border-bottom: 1px solid #f4f4f4;
}

.box-tools-left {
  position: absolute;
  left: 10px;
  top: 5px;
}

.box-tools-right {
  position: absolute;
  right: 10px;
  top: 5px;
}

.btn-default {
  background-color: #f4f4f4;
  color: #444;
  height: 27px;
  display: inline-block;
  margin-bottom: 0;
  margin-left: -1px;
  font-weight: 400;
  font-size: 11px;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  border: 1px solid #ddd;
  line-height: 1.42857143;
  border-radius: 4px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.box-title {
  display: inline-block;
  font-size: 18px;
  margin: 0;
  line-height: 1;
}

.box-body {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  padding: 10px;
}

.table {
  color: #3a4859;
  font-family: Arial;
  font-family: Arial;
  font-size: 12px;
  text-align: center;
}

.table tbody tr td,
.table tbody tr th {
  padding: 0.5rem;
}

.table tbody tr:hover td,
.table tbody tr:hover th {
  background-color: $cor-paleta-light-teal;
}

.table-action + .table-action {
  margin-left: 10px;
}

.table-action:hover {
  cursor: pointer;
}

.page-link {
  border: 0;

  outline: none !important;
  box-shadow: none !important;
}

.page-item.active .page-link {
  background-color: #ddd;
  border-color: #ddd;
  /*color: $cor-paleta-rhino;*/
  font-weight: bold;
}

.page-item:not(.disabled) .page-link {
  color: $cor-paleta-rhino !important;
}

.pull-right {
  float: right !important;
}

.box-footer {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  border-top: 1px solid #f4f4f4;
  padding: 10px;
  background-color: #fff;
}

h3 {
  //font-family: "Source Sans Pro", sans-serif;
}

.pagination > li > span,
.pagination > li > button {
  position: relative;
  float: left;
  padding: 6px 12px;
  margin-left: -1px;
  line-height: 1.42857143;
  color: #337ab7;
  text-decoration: none;
  background-color: #fff;
  border: 1px solid #ddd;
  font-size: 11px;
}

.box.box-primary {
  border-top-color: #3c8dbc;
}

.box.box-info {
  border-top-color: #00c0ef;
}

.box.box-danger {
  border-top-color: #dd4b39;
}

.box.box-warning {
  border-top-color: #F39200;
}

.box.box-success {
  border-top-color: #28a745;
}
</style>