<template>
  <b-row>
    <b-col align-self="center" lg="4" offset-lg="4" md="6" offset-md="3" sm="12">
      <b-card title="Login">
        <br />
        <b-form class="form-app" @submit.prevent="login">
          <b-form-group label="Usuário:" label-for="usuario">
            <b-form-input
              id="usuario"
              v-model.trim="$v.user.username.$model"
              placeholder="username"
              :state="$v.user.username.$dirty ? !$v.user.username.$error : null" autocomplete="off"
            ></b-form-input>
            <b-form-invalid-feedback v-if="!$v.user.username.required">Usuário é obrigatório</b-form-invalid-feedback>
          </b-form-group>
          <b-form-group label="Senha:" label-for="senha">
            <b-form-input
              id="senha"
              v-model="$v.user.password.$model"
              type="password"
              :state="$v.user.password.$dirty ? !$v.user.password.$error : null"
            ></b-form-input>
            <b-form-invalid-feedback>Senha é obrigatória</b-form-invalid-feedback>
          </b-form-group>
          <b-button type="submit" variant="success">Login</b-button>
        </b-form>
      </b-card>
    </b-col>
  </b-row>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  name: "Login",
  data() {
    return {
      loading: false,
      message: "",
      user: {
        username: "",
        password: ""
      }
    };
  },
  validations: {
    user: {
      username: {
        required: required
      },
      password: {
        required
      }
    }
  },
  mounted() {
    if (this.$store.state.auth.user) {
      this.$router.replace("/");
    }
    if (navigator.userAgent.toLowerCase().indexOf('trident') > -1) {
        alert("O Internet Explorer não é compatível com esta aplicação. Por favor acesse através do Mozilla Firefox ou Google Chrome.");
        window.location("https://www.mozilla.org/pt-BR/");
    }
  },
  methods: {
    async login() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        try {
          await this.$store.dispatch("auth/login", this.user);
          this.$router.push(this.$route.query.redirect || "/");
        } catch (error) {
          this.$bvToast.toast(
            error.data ? error.data.message || error.data : error.statusText,
            {
              title: "Erro",
              variant: "danger",
              solid: true
            }
          );
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
</style>
