<template lang="html">
  <b-form-input 
    v-model.lazy="formattedValue"
    v-money="money"
    :value="value"
    :state="state"
    ></b-form-input>
</template>

<script>
export default {
  name: "CompMoney",
  props: ['state', 'value'],
  data() {
    return {
      formattedValue: this.value ? String(this.value.toFixed(2)) : "0.00",
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "R$ ",
        precision: 2
      }
    };
  },
  watch: {
    formattedValue: {
      immediate: true,
      handler(newValue) {
        this.$emit('input', newValue.replace(/[^\d,]/g, '').replace(',', '.'))
      }
    }
  }
};
</script>